import * as React from 'react'
import {
  Datagrid,
  DateField,
  Filter,
  FilterProps,
  List,
  ListProps,
  TextField,
  TextInput,
  useListContext
} from 'react-admin'
import { ResourceName } from '../../../core/ResourceName'

const ListFilters = (props: Omit<FilterProps, 'children'>) => (
  <Filter {...props}>
    <TextInput label="Nazwa" source="name" alwaysOn />
  </Filter>
)

const OverallViewGrid = () => {
  const {
    loading,
  } = useListContext()
  return (
    <Datagrid
      loaded={!loading}
      rowClick={(id) => `${ResourceName.STATS_OVERALL_VIEW}/${id}/show`}
    >
      <TextField label="Id" source="id" sortable={false} />
      <TextField label="Nazwa" source="name" sortable={false} />
      <DateField label="Data rozpoczcia" source="dateStart" showTime sortable={false} />
      <DateField label="Data zakoczenia" source="dateEnd" showTime sortable={false} />
    </Datagrid>
  )
}

export const OverallViewList = (props: ListProps) => (
  <List {...props} actions={false} pagination={false} filters={<ListFilters />}>
    <OverallViewGrid />
  </List>
)
